import '@mdi/font/css/materialdesignicons.css';
import Vue from 'vue';
import VueRouter from 'vue-router';
import vuetify from './plugins/vuetify';
import firebase from 'firebase/app';

Vue.config.productionTip = false;
Vue.use(VueRouter);

const config = {
    apiKey: 'AIzaSyAhjZhtId1T-VZkTISO-6_DqZKJHmNU3e4',
    authDomain: 'inol-logger.firebaseapp.com',
    databaseURL: 'https://inol-logger.firebaseio.com',
    projectId: 'inol-logger',
    storageBucket: '',
    messagingSenderId: '411681502470',
    appId: '1:411681502470:web:7e8857ab742c1015',
};
firebase.initializeApp(config);

import App from './App.vue';
import Calculator from './components/calculator.vue';
import WeeklySummary from './components/WeeklySummary.vue';
import DailyLog from './components/DailyLog.vue';
import ExerciseLog from './components/ExerciseLog.vue';
import MyWorkout from './components/MyWorkout.vue';

const routes = [
    { path: '/', component: Calculator, meta: { title: 'INOL Calculator' } },
    { path: '/calculator', name: 'Calculator', component: Calculator, meta: { title: 'INOL Calculator' } },
    { path: '/weekly_summary', name: 'WeeklySummary', component: WeeklySummary, meta: { title: 'Weekly Summary', requiresAuth: true } },
    { path: '/daily_log', name: 'DailyLog', component: DailyLog, meta: { title: 'Daily Log', requiresAuth: true } },
    { path: '/exercise_log', name: 'ExerciseLog', component: ExerciseLog, meta: { title: 'Exercise Log', requiresAuth: true } },
    { path: '/my_exercise', name: 'MyExercise', component: MyWorkout, meta: { title: 'My Exercise', requiresAuth: true } },
    { path: '*', redirect:'/' },
];

const router = new VueRouter({
    routes, // `routes: routes`의 줄임
});

new Vue({
    vuetify,
    router,
    render: h => h(App),
}).$mount('#app');
