<template>
    <v-app id="inspire">
        <v-navigation-drawer v-model="drawer" app clipped>
            <v-list dense>
                <v-list-group v-if="user" :value="true" no-action prepend-icon="mdi-account-circle">
                    <template v-slot:activator>
                        <v-list-item-content>
                            <v-list-item-title>
                                {{ user.displayName }}
                            </v-list-item-title>
                        </v-list-item-content>
                    </template>
                    <v-list-item link :to="{ name: 'WeeklySummary' }">
                        <v-list-item-action>
                            <v-icon>mdi-calendar-week</v-icon>
                        </v-list-item-action>
                        <v-list-item-content>
                            <v-list-item-title>Weekly Summary</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item @click="logout">
                        <v-list-item-action>
                            <v-icon>mdi-logout</v-icon>
                        </v-list-item-action>
                        <v-list-item-content>
                            <v-list-item-title>Sign-out</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-group>
                <v-list-item v-else @click="dialogForSiginIn = true">
                    <v-list-item-action>
                        <v-icon>mdi-login</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>Sign-in</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item :to="{ name: 'Calculator' }">
                    <v-list-item-action>
                        <v-icon>mdi-calculator</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>Calculator</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
                <v-list-group no-action :value="false" prepend-icon="mdi-history">
                    <template v-slot:activator>
                        <v-list-item-content>
                            <v-list-item-title>Log</v-list-item-title>
                        </v-list-item-content>
                    </template>

                    <v-list-item link :to="{ name: 'DailyLog' }">
                        <v-list-item-action>
                            <v-icon>mdi-calendar</v-icon>
                        </v-list-item-action>
                        <v-list-item-content>
                            <v-list-item-title>Daily Log</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item link :to="{ name: 'ExerciseLog' }">
                        <v-list-item-action>
                            <v-icon>mdi-calendar-text</v-icon>
                        </v-list-item-action>
                        <v-list-item-content>
                            <v-list-item-title>Exercise Log</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-group>
            </v-list>
        </v-navigation-drawer>

        <v-app-bar app clipped-left>
            <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
            <v-toolbar-title>{{ $route.meta.title }}</v-toolbar-title>
            <v-spacer></v-spacer>
            <template v-if="$route.meta.title === 'INOL Calculator'">
                <v-btn icon @click="clear">
                    <v-icon>mdi-delete</v-icon>
                </v-btn>
                <v-btn icon @click="save">
                    <v-icon>mdi-content-save</v-icon>
                </v-btn>
            </template>
            <template v-if="$route.meta.title === 'My Exercise'">
                <v-btn icon @click="deleteMyExercise">
                    <v-icon>mdi-delete</v-icon>
                </v-btn>
                <v-btn icon @click="addMyExercise">
                    <v-icon>mdi-plus</v-icon>
                </v-btn>
            </template>
        </v-app-bar>

        <v-content>
            <v-container text-center fluid pa-0>
                <div class="text-center">
                    <v-dialog v-model="dialogForSiginIn" width="500">
                        <v-card>
                            <v-card-text>
                                <v-container>
                                    <v-row>
                                        <v-btn text @click="login('google')"> <v-icon left>mdi-google</v-icon> Sign in with Google </v-btn>
                                    </v-row>
                                    <v-row>
                                        <v-btn text @click="login('facebook')"> <v-icon left>mdi-facebook</v-icon> Sign in with Facebook </v-btn>
                                    </v-row>
                                    <v-row>
                                        <v-btn text @click="login('twitter')"> <v-icon left>mdi-twitter</v-icon> Sign in with Twitter </v-btn>
                                    </v-row>
                                    <v-row>
                                        <v-btn text @click="login('github')"> <v-icon left>mdi-github-box</v-icon> Sign in with Github </v-btn>
                                    </v-row>
                                </v-container>
                                No Sign-up is required
                            </v-card-text>
                        </v-card>
                    </v-dialog>
                    <v-dialog v-model="dialogForLogin" width="500">
                        <v-card>
                            <v-card-text>
                                Sign-in is needed
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn text @click="dialogForLogin = false">
                                    OK
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </div>
                <v-fade-transition mode="out-in">
                    <router-view></router-view>
                </v-fade-transition>
            </v-container>
        </v-content>
    </v-app>
</template>

<script>
import firebase from 'firebase/app';
import 'firebase/auth';

import global from './global.js';

export default {
    data: () => ({
        dialogForSiginIn: false,
        dialogForLogin: false,
        drawer: null,
        user: null,
    }),
    methods: {
        save: function() {
            global.bus.$emit('calculatorSave');
        },
        clear: function() {
            global.bus.$emit('calculatorClear');
        },
        addMyExercise: function() {
            global.bus.$emit('addMyExercise');
        },
        deleteMyExercise: function() {
            global.bus.$emit('deleteMyExercise');
        },
        login: function(social) {
            let provider;
            switch (social) {
                case 'google':
                    provider = new firebase.auth.GoogleAuthProvider();
                    break;
                case 'facebook':
                    provider = new firebase.auth.FacebookAuthProvider();
                    break;
                case 'twitter':
                    provider = new firebase.auth.TwitterAuthProvider();
                    break;
                case 'github':
                    provider = new firebase.auth.GithubAuthProvider();
                    break;
                default:
                    return;
            }
            firebase
                .auth()
                .signInWithPopup(provider)
                .then(() => {
                    //Browser를 닫으면 세션이 종료되도록 설정
                    // firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION);
                    this.dialogForSiginIn = false;
                    this.drawer = false;
                })
                .catch(err => {
                    alert(err.message);
                });
        },
        logout: function() {
            if (this.user) {
                firebase.auth().signOut();
                // if(this.$route.path !== 'calculator'){
                //     this.$router.push({ path: 'calculator' });
                // }
                this.drawer = false;
            }
        },
    },
    created() {
        this.$vuetify.theme.dark = true;
        /**
         * 새로고침 하거나, 브라우저를 껐다가 켜도 세션에서 사용자 정보를 읽어오기 때문에 onAuthStateChanged를 등록해준다.
         */
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                this.user = user;
                // if(this.$route.path !== 'weekly_summary'){
                //     this.$router.push({ path: 'weekly_summary' });
                // }
            } else {
                this.user = null;
                // if(this.$route.path !== 'calculator'){
                //     this.$router.push({ path: 'calculator' });
                // }
            }
        });
        this.$router.beforeEach((to, from, next) => {
            try{
                if (to.matched.some(record => record.meta.requiresAuth)) {
                    if (!this.user) {
                        this.dialogForLogin = true;
                        next(false);
                    } else {
                        next();
                    }
                } else {
                    next();
                }
            }catch(e){
                console.log(JSON.stringify(e));
            }

        });
    },

};
</script>
